html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  min-height: 100vh;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  display: flex;
  justify-content: center;
  align-items: center; }

@keyframes gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

.container {
  background: #ffd2d2;
  background: radial-gradient(circle, #ffd2d2 16%, #ffa0a0 75%);
  width: 800px;
  height: 600px;
  border-radius: 10px;
  box-shadow: 5px 5px 10px 0px black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.label {
  font-family: "Source Serif Pro", serif;
  font-size: 40px;
  max-height: 100px;
  height: 100px;
  padding: 0;
  margin: 0; }

.editor {
  width: 600px;
  height: 250px;
  padding: 10px;
  resize: none;
  font-family: "PT Sans", sans-serif;
  border-radius: 10px;
  border: solid 2px #ff6ea6;
  background-color: #f0efef; }

.buttons {
  display: flex;
  justify-content: space-around;
  margin-top: 10px; }
  .buttons__button {
    width: 100px;
    height: 40px;
    border: none;
    margin: 10px;
    font-style: "PT Sans", sans-serif;
    font-size: 18px;
    background: #fff; }
    .buttons__button:hover {
      background-color: #e73c7e;
      color: #fff;
      animation: hover 1s; }

@keyframes hover {
  from {
    background: #fff; }
  to {
    background-color: #e73c7e;
    color: #fff; } }
